"use client";

import { Metadata } from "next";
import BlogLayout from "@/components/blog/BlogLayout";
import BlogCard from "@/components/blog/BlogCard";
import { getAllBlogPosts } from "@/utils/blog";
import { SEO_STRINGS } from "@/constants/strings/seo";

export const metadata: Metadata = {
  title: `Blog - ${SEO_STRINGS.docs.title}`,
  description: "Read the latest articles and tutorials about Simple Table",
  keywords: `blog, articles, tutorials, ${SEO_STRINGS.docs.keywords}`,
};

const BlogPage = async () => {
  const posts = await getAllBlogPosts();

  return (
    <BlogLayout>
      <div className="space-y-8">
        <div className="text-center">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">Blog</h1>
          <p className="text-xl text-gray-600">Latest articles and tutorials about Simple Table</p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {posts.map((post) => (
            <BlogCard key={post.slug} post={post} />
          ))}
        </div>

        {posts.length === 0 && (
          <div className="bg-white rounded-lg shadow p-6">
            <p className="text-center text-gray-500">
              No blog posts available yet. Check back soon!
            </p>
          </div>
        )}
      </div>
    </BlogLayout>
  );
};

export default BlogPage;
